"use client";
import { useState } from "react";
import axios from "axios";
import "../globals.css";
import ReactMarkdown from "react-markdown";

export default function Upload() {
  const [file, setFile] = useState(null);
  const [message, setMessage] = useState("");
  const [analysis, setAnalysis] = useState("");

  const handleFileChange = (e) => {
    setFile(e.target.files[0]);
  };

  const handleUpload = async () => {
    if (!file) {
      setMessage("Please Select a File First");
      return;
    }

    const formData = new FormData();
    formData.append("resume", file);

    try {
      const response = await axios.post(
        "http://localhost:5000/api/upload",
        formData,
        {
          headers: { "Content-type": "multipart/form-data" },
        }
      );

      setMessage(response.data.message);
      console.log("File Uploaded Successfully:", response.data);

      //extracted text from resume
      const resumeText = response.data.text;

      //forwarding extracted text to OPENAI if valid
      if (resumeText) {
        analyzeResume(resumeText);
      }
    } catch (error) {
      console.error("Upload Error:", error);
      setMessage("Upload Failed. Please Try Again");
    }
  };

  const analyzeResume = async (text) => {
    try {
      const response = await axios.post(
        "http://localhost:5000/api/analyze",
        { text },
        { headers: { "Content-Type": "application/json" } }
      );
      setAnalysis(response.data.analysis);
    } catch (error) {
      console.error("AI Analysis Error:", error);
      setAnalysis("Failed to analyze resume. please try again.");
    }
  };

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-gray-100">
      <form onSubmit={(e) => e.preventDefault()}>
        <h1 className="text-2xl font-bold mb-4">Upload Your Resume</h1>
        <input
          type="file"
          accept=".pdf,.doc,.docx"
          className="mb-4 p-2 border rounded"
          onChange={handleFileChange}
        />
        <button
          type="button"
          onClick={handleUpload}
          className="px-4 py-2 bg-blue-500 text-white rounded"
        >
          Upload
        </button>
      </form>
      {message && <p className="mt-4"> {message} </p>}
      {analysis && (
        <div className="mt-6 p-4 bg-green-100 shadow rounded">
          <h2 className="text-lg font-bold">AI Feedback</h2>
          <ReactMarkdown className="text-sm">{analysis}</ReactMarkdown>
        </div>
      )}
    </div>
  );
}
